.ref-container {
  flex-direction: column;
  grid-area: 1 / 1 / 13 / 13;
  justify-content: space-between;
  padding: 0 3rem 0 5rem;
  display: flex;
}

.ref-title {
  color: var(--heading-color);
  font-size: 4rem;
}

.ref-content {
  height: 60vh;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  display: flex;
}

.ref-col {
  width: 100%;
  flex-direction: column;
  align-items: flex-start;
  display: flex;
}

.ref-col h3 {
  color: var(--subheading-color);
  margin-bottom: .9rem;
  font-size: 2rem;
}

.ref-col a {
  color: var(--background-color);
  width: 90%;
  text-decoration: none;
}

.ref-item {
  background-color: #ffffffe6;
  border: 1px solid #ffffffe6;
  border-radius: 5px;
  align-items: center;
  margin-bottom: 1rem;
  padding: .8rem;
  font-size: 1.2rem;
  transition: transform .3s ease-in-out;
  display: flex;
}

.ref-icon {
  height: 1.5rem;
  aspect-ratio: 1 / 1;
  margin-right: 1rem;
}

a .ref-item:hover {
  background-color: #fffc;
  border: 1px solid #fffc;
  transform: translateX(.5rem);
}

p.ref-disclaimer {
  width: 95%;
  color: #ffffffb3;
  margin: 2rem 0 1rem;
  font-size: .8rem;
  font-style: italic;
}

